}

.list-group-item.board:hover {
  cursor: -webkit-grab;
  background-color: gainsboro;
}

.fa.fa-minus-circle {
  color: red;
}

.landing {
  position: relative;
  /* background: url("../img/showcase.jpg") no-repeat; */
  background-size: cover;
  background-position: center;
  height: 100vh;
  margin-top: -24px;
  margin-bottom: -50px;
}

.landing-inner {
  padding-top: 80px;
}

.dark-overlay {
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.card-form {
  opacity: 0.9;
}

.latest-profiles-img {
  width: 40px;
  height: 40px;
}

.form-control::-webkit-input-placeholder {
  color: #bbb !important;
}

.form-control:-ms-input-placeholder {
  color: #bbb !important;
}

.form-control::-ms-input-placeholder {
  color: #bbb !important;
}

.form-control::placeholder {
  color: #bbb !important;
}



*{
  margin:0;
  padding:0;
  box-sizing:border-box;

}

html{
  font-size:13px;

}

header{
  width:100%;
  height:10rem;
  position: relative;
  background-color: #f7f7f7;
  overflow: hidden;
}
.logo{
    float: left;
    margin-right: 10px;
    /* margin-top:1rem; */
  
  }

.menu{
  display: none;

}

.main-text{
  width:100%;
  text-align:center;
  position:absolute;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%);
  color:#333;
  font-family: "Lato", sans-serif;
  text-transform: uppercase;
  letter-spacing: 3px;

}

.main-text h1{
  font-size:5rem;
  margin-right:-3px;

}

.main-text h3{
  font-size:2.5rem;
  margin-right: -3px;

}

nav{
  width:100%;
  height:10rem;
  font-family: "Roboto Condensed", sans-serif;
  font-size:2rem;
  font-weight:100;
  text-transform:uppercase;
  line-height: 10rem;
  /* box-shadow: 0 0 3rem rgba(0,0,0,0.2); */
  background-color: #fff;

}


.nav-container{
  width:80%;
  max-width:96rem;
  margin: 0 auto;
}

/* .logo{
  float: left;
  margin-top:1rem;

} */


.logo img{
  width:5rem;
  height:5rem;

}

.dashboard {
    margin-left: 100px;
    display: inline-block;
}

nav ul{
  list-style: none;
  float:right;
}

nav ul li{
  display: inline-block;
  padding: 0 1.5rem;
  transition: opacity 0.3s; 
}

nav ul li a{
  text-decoration: none;
  color: #333;

}

nav:hover ul li:not(:hover){
  opacity: 0.5;
}

@media screen and (max-width:768px){
  .logo{
    display: none;

  }
  .menu{
    display:block;
    position: fixed;
    width:3rem;
    height:3rem;
    top:3rem;
    right:3rem;
    z-index:999;
    cursor:pointer;


  }

  .bar,
  .bar::before,
  .bar::after{
    position: absolute;
    background-color: #000;
    margin:auto;
    width:100%;
    height:3px;
    content:'';
    top:50%;
    left:0;
  }
  .bar{
    margin-top:-2px;
  }

  .bar::before{
    top: -1.2rem;
  }

  .bar::after{
    top:1.2rem;
  }


  .bar::before,
  .bar::after{
    transform:rotate(0deg);
  }

  .active .bar{
    background-color: transparent;
  }



  .active .bar::before{
    transform:rote(45deg);
  }

  .active .bar::after{
    transform:rote(-45deg);
  }
  .active .bar::before,
  .active .bar::after{
    top:0;
  }
  nav{
    width:25rem;
    height:100%;
    background-color: #fff;
    position:fixed;
    transform:translateX(-50rem);
    z-index:998;
    line-height:1;
    font-size:2.5rem;
  }

  .menu-open nav{
    transform:translateX(0);

  }

  nav ul{
    width: 100%;
    text-align:center;
    padding-top:10rem;
  }

  nav ul li{
    display: block;
    padding: 4rem 1rem;

  }

  .main-text h1{
    font-size:2.5rem;
  }

  .main-text h3{
    font-size:1.5rem;
  }

}


